import Rstore from 'store'
import history from '@script/history'

export const formatDate = str => {
  const date = new Date(str),
    time = new Date().getTime() - date.getTime() //- =  = 
  if (time < 0) {
    return ''
  } else if (time / 1000 < 60) {
    return ''
  } else if (time / 60000 < 60) {
    return parseInt(time / 60000) + ''
  } else if (time / 3600000 < 24) {
    return parseInt(time / 3600000) + ''
  } else if (time / 86400000 < 31) {
    return parseInt(time / 86400000) + ''
  } else if (time / 2592000000 < 12) {
    return parseInt(time / 2592000000) + ''
  } else {
    return parseInt(time / 31536000000) + ''
  }
}

export const showMsg = msg => {
  typeof tipTime !== 'undefined' && tipTime && clearTimeout(this.tipTime)
  const formMsg = document.getElementById('form-msg')
  if (!formMsg) {
    const sDiv = document.createElement('div')
    sDiv.id = 'form-msg'
    sDiv.className = 'tc form-msg'
    sDiv.innerHTML =
      "<div layout-align='center center' layout>" + msg + '</div>'
    document.getElementsByTagName('body')[0].appendChild(sDiv)
  } else {
    formMsg.style.display = 'block'
    formMsg.innerHTML =
      "<div layout-align='center center' layout>" + msg + '</div>'
  }
  const tipTime = setTimeout(
    () => (document.getElementById('form-msg').style.display = 'none'),
    1000
  )
}

export const setStorage = (key, value) => {
  // key[]value[]
  const storeTime = new Date().getTime()
  value['setKeyTime'] = storeTime
  Rstore.set(key, value)
}
export const getStorage = (key, exp, name) => {
  //:key[]exp[]name[]
  const getKeyData = Rstore.get(key)
  exp &&
    getKeyData &&
    new Date().getTime() - getKeyData.setKeyTime > exp &&
    Rstore.remove(key) &&
    (name && (name = Rstore.get(key)))
  return getKeyData
}

export const userExp = e => {
  if (!Rstore.get('saveUser') && e) {
    showMsg('<br />')
    history.push({ pathname: '/user/login' })
  }
  return Rstore.get('saveUser')
}



